.contact {
  padding: 5% 0 15% 0;
}

.header {
  font-family: Helvetica Neue;
  font-weight: bold;
  font-size: 36px;
  padding: 10%;
}

.inputForm {
  font-family: AvenirNext-Regular;
  font-size: 25px;
  margin-top: -15px;
}

.contactBody {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  width: 100%;
}

.contactForm {
  display: flex;
  flex-direction: column;
}

.messageName,
.messageEmail,
.messageInput {
  background: #ffffff;
  border: 1px solid #979797;
  box-shadow: inset 0 1px 3px 0 rgba(0, 0, 0, 0.5);
  border-radius: 3px;
  font-family: AvenirNext-Regular;
  font-size: 15px;
}

.messageName,
.messageEmail {
  padding: 10px 0 10px 5px;
  margin-bottom: 2%;
}

.messageInput {
  padding: 10px 0 200px 5px;
}

.buttonContainer {
  margin: 10px 0 10px 0;
  display: flex;
  justify-content: flex-end;
}

.buttonPrimary {
  padding: 10px 20px;
  font-family: AvenirNext-Regular;
  font-size: 15px;
  border-radius: 10px;
  background-color: #e7e7e7;
}

.buttonPrimary:hover {
  cursor: pointer;
}

.photo img {
  width: 100%;
}
